body {
	background: #FFF;
	font-size: 16px;
	color: #7c8092; }

a {
	text-decoration: none;
	font-size: inherit;
	color: inherit; }

@mixin bold {
	font-family: 'Barlow', sans-serif;
	font-weight: 900; }

@mixin semi {
	font-family: 'Barlow', sans-serif;
	font-weight: 600; }

@mixin RobotoReg {
	font-family: 'Roboto', sans-serif;
	font-weight: 400; }

h1 {
	font-size: 60px;
	text-transform: uppercase;
	@include bold;
	line-height: 1.34; }

h3 {
	font-size: 48px;
	@include semi;
	line-height: 1.34; }

h4 {
	font-size: 36px;
	@include semi;
	line-height: 1.34; }

.container {
	margin: 0 auto;
	padding: 0 40px;
	max-width: 1250px; }

.text {
	@include RobotoReg;
	line-height: 1.88; }


.slick-slide {
	margin: 0 43px; }
.slick-list {
	margin: 0 -43px;
	overflow: visible; }

.out {
	width: 100%;
	overflow: hidden;
	padding-bottom: 100px; }
