}

.icon-youtube {
  width: 1em;
  height: 1em;
  fill: #444;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

/* Slider */
.slick-loading .slick-list {
  background: #fff url("../img/ajax-loader.gif") center center no-repeat;
}

/* Icons */
@font-face {
  font-family: "slick";
  src: url("../fonts/slick.eot");
  src: url("../fonts/slick.eot?#iefix") format("embedded-opentype"), url("../fonts/slick.woff") format("woff"), url("../fonts/slick.ttf") format("truetype"), url("../fonts/slick.svg#slick") format("svg");
  font-weight: normal;
  font-style: normal;
}

/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  margin-top: -10px;
  padding: 0;
  border: none;
  outline: none;
}

.slick-prev:hover, .slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}

.slick-prev:hover:before, .slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}

.slick-prev:before, .slick-next:before {
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  color: white;
  opacity: 0.75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -25px;
}

[dir="rtl"] .slick-prev {
  left: auto;
  right: -25px;
}

.slick-prev:before {
  content: "←";
}

[dir="rtl"] .slick-prev:before {
  content: "→";
}

.slick-next {
  right: -25px;
}

[dir="rtl"] .slick-next {
  left: -25px;
  right: auto;
}

.slick-next:before {
  content: "→";
}

[dir="rtl"] .slick-next:before {
  content: "←";
}

/* Dots */
.slick-dots {
  position: absolute;
  bottom: 22px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  width: 100%;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.slick-dots li button {
  display: block;
  padding: 3px;
  background: none;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: red;
  cursor: pointer;
}

.slick-dots li button:before {
  content: "";
  display: block;
  height: 10px;
  width: 10px;
  box-sizing: border-box;
  border-radius: 50%;
  background: #979795;
  transition: all 0.1s ease;
}

.slick-dots li button:hover:before {
  background: #4E4E4E;
}

.slick-dots li.slick-active button:before {
  background: #4E4E4E;
}

body {
  background: #FFF;
  font-size: 16px;
  color: #7c8092;
}

a {
  text-decoration: none;
  font-size: inherit;
  color: inherit;
}

h1 {
  font-size: 60px;
  text-transform: uppercase;
  font-family: 'Barlow', sans-serif;
  font-weight: 900;
  line-height: 1.34;
}

h3 {
  font-size: 48px;
  font-family: 'Barlow', sans-serif;
  font-weight: 600;
  line-height: 1.34;
}

h4 {
  font-size: 36px;
  font-family: 'Barlow', sans-serif;
  font-weight: 600;
  line-height: 1.34;
}

.container {
  margin: 0 auto;
  padding: 0 40px;
  max-width: 1250px;
}

.text {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  line-height: 1.88;
}

.slick-slide {
  margin: 0 43px;
}

.slick-list {
  margin: 0 -43px;
  overflow: visible;
}

.out {
  width: 100%;
  overflow: hidden;
  padding-bottom: 100px;
}

.main {
  margin-bottom: 30px;
}

.main__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.aside {
  width: 25%;
}

.content {
  width: 70%;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 50px;
  color: #FFFFFF;
  z-index: 1;
}

.header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__nav {
  display: flex;
  align-items: center;
}

.header__nav .nav {
  margin-right: 60px;
}

.logo {
  font-family: 'Barlow', sans-serif;
  font-weight: 900;
  font-size: 24px;
  text-transform: uppercase;
}

.logo span {
  display: inline-block;
  background-color: #032c9e;
  padding: 12px;
  margin-right: 12px;
}

.hero {
  color: #FFFFFF;
  padding: 300px 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  background-color: #032c9e;
  overflow: hidden;
}

.hero__content {
  text-transform: uppercase;
  max-width: 1030px;
  margin: 0 auto;
  padding: 0 40px;
  text-align: center;
}

.hero__suptitle {
  font-family: 'Barlow', sans-serif;
  font-weight: 900;
  font-size: 18px;
  margin-bottom: 50px;
}

.hero__title {
  margin-bottom: 160px;
}

.hero__triangle {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0;
  border-top: 245px solid transparent;
  border-left: 2000px solid #FFFFFF;
  border-bottom: 0 solid transparent;
}

.person__avatar {
  border-radius: 50%;
  height: 96px;
  width: 96px;
  overflow: hidden;
  margin: 0 auto 30px;
}

.person__avatar img {
  height: 140%;
  width: 100%;
  object-fit: cover;
  transform: scale(1.2);
}

.person__desc {
  text-transform: uppercase;
  font-size: 14px;
  font-family: 'Barlow', sans-serif;
  font-weight: 900;
}

.nav__item {
  display: inline-block;
  text-transform: uppercase;
  font-family: 'Barlow', sans-serif;
  font-weight: 900;
}

.nav__link {
  padding: 0 20px;
  position: relative;
}

.nav__link:after {
  content: '';
  position: absolute;
  height: 2px;
  background-color: #FFFFFF;
  width: 0;
  transition: 0.3s;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.nav__link:hover:after {
  width: 100%;
}

.socials {
  display: flex;
  align-items: center;
}

.socials__item {
  width: 16px;
  height: 16px;
}

.socials__item a {
  display: block;
  width: 100%;
  height: 100%;
}

.socials__item .icon {
  width: 100%;
  height: 100%;
  fill: #fff;
}

.socials__item:not(:last-child) {
  margin-right: 30px;
}

.article {
  margin-bottom: 80px;
}

.article__title {
  color: #1b2550;
  margin-bottom: 45px;
}

.article__text {
  margin-bottom: 75px;
}

.article__video {
  width: 120%;
  position: relative;
  padding-top: 69%;
}

.article__video iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.small-article__video {
  width: 100%;
  position: relative;
  padding-top: 100%;
  margin-bottom: 70px;
}

.small-article__video img {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.small-article__image {
  width: 100%;
  position: relative;
  padding-top: 50%;
  margin-bottom: 65px;
}

.small-article__image img {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.small-article__title {
  margin-bottom: 40px;
  color: #303342;
}

.small-article__date {
  margin-bottom: 45px;
  font-family: 'Barlow', sans-serif;
  font-weight: 900;
  text-transform: uppercase;
  color: #484b59;
  letter-spacing: 2px;
}

.small-article__text {
  margin-bottom: 45px;
  max-height: 185px;
  overflow: hidden;
}

.more-link {
  transition: 0.3s;
  color: #032c9e;
  font-family: 'Barlow', sans-serif;
  font-weight: 900;
  font-size: 14px;
  text-transform: uppercase;
  position: relative;
  padding: 0 30px;
  z-index: 1;
}

.more-link:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background-color: #032c9e;
  transition: 0.3s;
  z-index: -1;
}

.more-link:hover {
  color: #FFFFFF;
}

.more-link:hover:before {
  width: 100%;
}

.last-news__title {
  font-family: 'Barlow', sans-serif;
  font-weight: 900;
  font-size: 60px;
  padding-bottom: 40px;
  position: relative;
  margin-bottom: 100px;
}

.last-news__title:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  height: 4px;
  width: 126px;
  background-color: #33cbcc;
}
